import { GatsbyImage, getImage } from "gatsby-plugin-image";
import React from "react";

const OurExperince = ({ data }) => {
  const imageData = getImage(data.image);
  return (
    <section className="hire-section-wrapper item-center gap-2xl">
      <h2 className="about-h2 c-text-center md-c-text-start">{data.title}</h2>
      <div className="flex-center md-flex-column gap-lg">
        <GatsbyImage
          image={imageData}
          alt={data.imageAlt || data.title}
          loading="eager"
          className="w-full"
          placeholder="none"
          fetchpriority="high"
        />
        <div className="hire-cards flex-column w-full">
          {data.boxdata.map((data) => (
            <div key={data.id} className="hire-card flex-column gap-sm">
              <h3 className="about-h5 font-black">{data.title}</h3>
              <p className="about-p-two">{data.desc}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default OurExperince;
