import React from "react";
import TalkToUsNew from "@components/common/talk-to-us-new";
import { Link } from "@reach/router";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

const Hero = ({ db, data }) => {
  // const data = data[0];
  // console.log("home", homeData);
  const imageData = getImage(data.image);
  // console.log("image", imageData);
  return (
    <section className="hire-section-wrapper flex-center md-flex-column gap-xl">
      <div className="item-start gap-lg w-full">
        <h1 className="about-h2-v2 font-black font-black">
          {data.title}
          <span className="primary-base">{data.colorText}</span>
        </h1>
        <p className="about-p font-black">{data.description}</p>
        <div className="buttons">
          <Link to="/contact-us" className="roll-button roll-btn">
            <span>Hire Now</span>
          </Link>
          <TalkToUsNew
            db={db}
            label="Free Consultation"
            customClass="normal-roll-button normal-roll-btn"
          />
        </div>
      </div>
      <div className="hire-hero-image">
        <GatsbyImage
          image={imageData}
          alt={data.imageAlt || data.title}
          loading="eager"
          className="w-full"
          placeholder="none"
          fetchpriority="high"
        />
      </div>
    </section>
  );
};

export default Hero;
