import React from "react";
import TalkToUsNew from "@components/common/talk-to-us-new";
import { Link } from "@reach/router";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

const BenifitsOfHiring = ({ db, data }) => {
  const imageData = getImage(data.image);
  return (
    <section className="hire-section-wrapper item-center gap-2xl">
      <div className="item-center gap-xl">
        <h2 className="about-h2 c-text-center md-c-text-start">{data.title}</h2>
        <p className="about-p c-text-center">{data.description}</p>
      </div>
      <div className="flex-center gap-lg lg-flex-column ">
        <div className="flex-column gap-md">
          <div className="benefit-cards c-grid grid-two gap-md w-full">
            {data.boxdata.map((data) => (
              <div key={data.id} className="benefit-card item-start gap-sm">
                <h3 className="about-h5 font-black">{data.title}</h3>
                <p className="about-p-three">{data.desc}</p>
              </div>
            ))}
          </div>
          <div className="buttons">
            <Link to="/contact-us" className="roll-button roll-btn">
              <span>Hire Now</span>
            </Link>
            <TalkToUsNew
              db={db}
              label="Free Consultation"
              customClass="normal-roll-button normal-roll-btn"
            />
          </div>
        </div>
        <GatsbyImage
          image={imageData}
          alt={data.imageAlt || data.title}
          loading="eager"
          className="w-full"
          placeholder="none"
          fetchpriority="high"
        />
      </div>
    </section>
  );
};

export default BenifitsOfHiring;
