export default HirePerformanceMarketer;
export const queryData = graphql`
  query HirePerformanceMarketerPageQuery {
    HirePerformanceMarketerImg: file(
      relativePath: { eq: "hire-performance-marketer.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    heroImage: file(
      relativePath: { eq: "hirescreens/hire-performance-marketer/hero.png" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }
    experienceImage: file(
      relativePath: {
        eq: "hirescreens/hire-performance-marketer/experience.png"
      }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }
    benefitsImage: file(
      relativePath: { eq: "hirescreens/hire-performance-marketer/benifits.png" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }

    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }

    allMetaJson(filter: { slug: { eq: "hire-performance-marketer" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
